import Vue from 'vue';
class Universal {
    request(method, url, params, callback) {
        console.log('Vue ==> ', Vue);
        
        if (method === 'POST') {
            if (params) {
                Vue.prototype.axios({
                        method: "POST",
                        url: url,
                        data: params
                    })
                    .then(callback)
                    .catch(err => {
                        
                    });
            } else {
                Vue.prototype.axios({
                        method: "POST",
                        url: url,
                    })
                    .then(callback)
                    .catch(err => {
                        
                    });
            }
        } else {
            if (params) {
                Vue.prototype.axios({
                        method: "GET",
                        url: url,
                        params: params
                    })
                    .then(callback)
                    .catch(err => {
                        console.log('err ==>', err);
                        
                    });
            } else {
                Vue.prototype.axios({
                        method: "GET",
                        url: url,
                        params: params
                    })
                    .then(callback)
                    .catch(err => {
                        console.log('err ==>', err);
                        
                    });
            }
        }
    }
}
export const universal = new Universal();